/* Divisa.css */

.divisa-container {
    display: flex;
    flex-direction: column;
    padding: 16px;
    margin: 16px 0;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    transition: box-shadow 0.3s ease-in-out;
  }
  
  .divisa-container:hover {
    box-shadow: 0 8px 12px rgba(21, 101, 192, 0.5); /* Azul oscuro con transparencia */
  }
  
  .vote-section {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }
  
  .index {
    font-size: 1.2rem;
    font-weight: 600;
    color: #333333;
    margin-right: 8px;
  }
  
  .like {
    cursor: pointer;
    padding: 8px 12px;
    border-radius: 4px;
    background-color: #f5f5f5;
    color: #333333;
    font-weight: 500;
    transition: background-color 0.3s ease-in-out;
  }
  
  .like:hover {
    background-color: #e0e0e0;
  }
  
  .like.gray {
    background-color: #b0bec5;
    color: #ffffff;
  }
  
  .info {
    display: flex;
    flex-direction: column;
  }
  
  .name {
    font-size: 1.1rem;
    font-weight: 700;
    color: #212121;
    margin-bottom: 4px;
  }
  
  .details {
    font-size: 0.9rem;
    color: #757575;
  }
  
  .details a {
    color: #2196F3;
    text-decoration: none;
    transition: color 0.3s ease-in-out;
  }
  
  .details a:hover {
    color: #1976D2;
  }