/* Header.css */

.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 24px;
    background-color: #1565C0; /* Azul oscuro */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .logo-container {
    display: flex;
    align-items: center;
  }
  
  .logo {
    font-size: 1.5rem;
    font-weight: 700;
    color: #ffffff;
    text-decoration: none;
    margin-right: 16px;
    transition: color 0.3s ease-in-out;
  }
  
  .logo:hover {
    color: #BBDEFB;
  }
  
  .menu-item {
    font-size: 1rem;
    font-weight: 500;
    color: #ffffff; /* Texto blanco */
    text-decoration: none;
    margin: 0 8px;
    padding: 8px 12px;
    border-radius: 4px;
    transition: all 0.3s ease-in-out;
  }
  
  .menu-item:hover {
    background-color: #ffffff; /* Fondo blanco al pasar el ratón */
    color: #000000; /* Texto negro al pasar el ratón */
  }
  
  .divider {
    color: #BBDEFB;
    margin: 0 8px;
  }
  
  .flex {
    display: flex;
    align-items: center;
  }
  
  .logout {
    cursor: pointer;
    color: #ffffff;
    font-weight: bold;
    padding: 8px 12px;
    border-radius: 4px;
    transition: all 0.3s ease-in-out;
  }
  
  .logout:hover {
    background-color: #E53935;
    color: #ffffff;
  }