/* Login.css */

body, html {
    height: 100%;
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #E3F2FD; /* Fondo azul claro */
  }
  
  .login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #E3F2FD; /* Fondo azul claro */
    padding: 24px;
  }
  
  .login-card {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 100%;
  }
  
  .login-title {
    font-size: 1.5rem;
    font-weight: 700;
    color: #1565C0; /* Azul oscuro */
    margin-bottom: 24px;
    text-align: center;
  }
  
  .login-input {
    width: 100%;
    padding: 12px;
    margin-bottom: 16px;
    border: 1px solid #BBDEFB; /* Azul claro */
    border-radius: 4px;
    font-size: 1rem;
    color: #333333;
    background-color: #ffffff;
    transition: border-color 0.3s ease-in-out;
  }
  
  .login-input:focus {
    border-color: #1565C0; /* Azul oscuro */
    outline: none;
  }
  
  .button-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .login-button {
    width: 100%;
    padding: 12px 24px;
    font-size: 1rem;
    font-weight: 600;
    color: #ffffff;
    background-color: #1565C0; /* Azul oscuro */
    border: none;
    border-radius: 24px; /* Bordes redondeados */
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
    margin-bottom: 16px;
  }
  
  .login-button:hover {
    background-color: #003c8f; /* Azul más oscuro */
  }
  
  .toggle-button {
    font-size: 0.9rem;
    color: #1565C0; /* Azul oscuro */
    background: none;
    border: none;
    cursor: pointer;
    text-decoration: underline;
    margin-bottom: 16px;
  }
  
  .toggle-button:hover {
    color: #003c8f; /* Azul más oscuro */
  }
  
  .support-text {
    font-size: 0.8rem;
    color: #757575;
    margin-top: 8px;
    text-align: center;
  }
  
  .support-link {
    color: #1565C0;
    text-decoration: none;
    font-weight: 600;
  }
  
  .support-link:hover {
    color: #003c8f;
  }