/* CreateDivisa.css */

.create-divisa-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #E3F2FD; /* Fondo azul claro */
    padding: 24px;
  }
  
  .create-divisa-card {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 100%;
  }
  
  .create-divisa-title {
    font-size: 1.5rem;
    font-weight: 700;
    color: #1565C0; /* Azul oscuro */
    margin-bottom: 24px;
    text-align: center;
  }
  
  .create-divisa-input {
    width: 100%;
    padding: 12px;
    margin-bottom: 16px;
    border: 1px solid #BBDEFB; /* Azul claro */
    border-radius: 4px;
    font-size: 1rem;
    color: #333333;
    background-color: #ffffff;
    transition: border-color 0.3s ease-in-out;
  }
  
  .create-divisa-input:focus {
    border-color: #1565C0; /* Azul oscuro */
    outline: none;
  }
  
  .create-divisa-button {
    width: 100%;
    padding: 12px 24px;
    font-size: 1rem;
    font-weight: 600;
    color: #ffffff;
    background-color: #1565C0; /* Azul oscuro */
    border: none;
    border-radius: 24px; /* Bordes redondeados */
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
    margin-top: 16px;
  }
  
  .create-divisa-button:hover {
    background-color: #003c8f; /* Azul más oscuro */
  }